.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
}
.projectDetail-main {
  width: 90%;
  padding: 10px;
  margin: 0px auto;
  margin-top: 2rem;
}

.projectDetail-content-main {
  /* width: 90%; */
  /* padding: 10px; */
  margin: 0px auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.projectDetail-main, .projectDetail-content-main {
  display: flex;
}
.projectDetail-content-text-p {
  padding-right: 10px;
  line-height: 1.5;
}
.subTitle {
  /* font-family: 'Abhaya Libre'; */
  color: #333 !important;
  font-weight: bold;
}


.projectDetail-main > *, .projectDetail-content-main > * {
  flex: 1;
  margin-bottom: 30px;
}
.button-projectDetail-div {
  display: flex;
  margin-top: 20px;
}

.projectDetail-text {
  font-size: 20px;
  line-height: 1.1;
  color: black !important;
}

.projectDetail-title-text {
  /* font-size: 70px; */
  line-height: 1.1;
  color: black !important;
}

.projectDetail-text-p {
  font-size: 20px;
  line-height: 30px;

  margin: 20px 10px 30px 20px;
  color: black !important;
  /* font-family: 'Abhaya Libre'; */
    line-height: 2rem;
}

.projectDetail-image-div > img {
  max-width: 100%;
  height: auto;
}

.wave-emoji {
  font-size: 30px;
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .projectDetail-text {
    font-size: 30px;
  }
  .projectDetail-title-text {
    /* font-size: 80px; */
  }
  .projectDetail-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-projectDetail-div {
    justify-content: space-around;
  }
  .projectDetail-text {
    font-size: 15px;
    text-align: center;
  }

  .projectDetail-title-text {
    font-size: 3rem;
  }

  .projectDetail-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: center;
  }
  .projectDetail-main {
    display: block;
  }
  .projectDetail-image-div {
    height: 256px;
  }
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}
